/* You can add global styles to this file, and also import other style files */
@use './tailwind.css';
@use 'primeicons/primeicons.css';

:root {
  --expense-color: var(--p-yellow-500, #ef4444);
  --income-color: var(--p-primary-400, #22c55e);
  --sidebar-width: 4px;
  --bar-radius: 6px;
}
.active {
  border-bottom: 0.3rem solid var(--p-primary-active-color);
}

.nav-item {
  font-weight: 200;
  line-height: 1.5rem;
  color: var(--p-primary-color);
  cursor: pointer;
  font-size: 1rem;
  display: inline-flex;
  padding: 0px 0.2rem;
  gap: 2px;
}

/*
allows pages to fill entire body with specifying w-full and h-full for each
*/
router-outlet ~ * {
  height: 100%;
  width: 100%;
}
